.transition,
.transition:hover,
.transition:active {
  transition: all 150ms ease-in-out;
}

html,
body {
  position: relative;
  height: 100%;
  background-color: #e5e5ec;
}
// HIDE CONTENT ONLOAD
.loading {
  position: fixed;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-image: url('../img/loading.svg');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-color: #e5e9ec;
}
// TOP NAV PANEL
nav.fixed-top {
  background-color: #fff;
  border: none;
}

.navbar-brand {
  height: auto;
  h1 {
    margin: 0;
    font-size: 1.5em;
    font-weight: 400;
    color: #fff;
  }
}
button.navbar-toggler {
  outline: 1px solid #1b1e24;
  background-color: #e5e9ec;
  padding: 0.125rem 0.375rem;
  margin: 0.75em;
  margin-left: 0
}
.navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(37,40,48, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

nav {
  .top-left-menu {
    background-color: #252830;
  }
  .header-buttons {
    color: #fff;
    font-size: 1rem;
  }
  form#search {
    padding: 0.9em;
    padding-left: 1.9rem;
    .form-control {      
      border: none;
      width: 70%;
      padding-left: 1.9em;
      background-color: #f3f3f3;
      box-shadow: none;
    }
    .form-control:focus {
      width: 100%;
      box-shadow: none;
    }
    .fas {
      position: absolute;
      z-index: 99;
      left: 2.4em;
    }
  }
}

#btn-notifications {
  &>.badge {
    font-size: 0.7em;
    padding: 0.18rem 0.35rem 0.2rem 0.35rem;
    border-radius: 50%;
    position: absolute;
    left: 6.5rem;
    top: 0;
    z-index: 99;
  }
  .dropdown-toggle:after {
    display: none;
  }
  .btn-link {
    font-weight: 500;
    color: #252830;
  }
  .btn-link:hover {
    text-decoration: none;
  }
}

#notification-list {
  max-height: 20em;
  overflow: auto;
  a {
    display: flex;
    opacity: 0.7;
    margin: 1rem;
    border-radius: 0.5rem;
    background-color: #efefef;
    position: relative;
    padding: 0.5rem 1.3rem;
    width: inherit;
  }
  a:hover {
    color: #262626;
    opacity: 1;
    text-decoration: none;
  }
  img {
    height: 35px;
    width: 35px;
    margin-right: 1em;
    margin-top: 1em;
    display: inline-block;
  }
  .notification-message {
    display: inline-block;
    white-space: normal;
    min-width: 20rem;
  }
  .badge {
    display: inline-block;
    position: absolute;
    border-radius: 50%;
    right: 0.5rem;
    top: 0.5rem;
    height: 1rem;
    width: 1rem;
  }
  em.since {
    font-size: 0.7em;
    color: #646c82;
  }
}

#nav-menu {
  justify-content: flex-end;
  align-items: center;
  li>a {
    padding: 0.75rem;
    color: #252830;
    font-weight: 500;
    display: block;
  }
  li>a:hover {
    text-decoration: none;
    background-color: #eee;
  }
  .divider {
    height: 0.05rem;
    margin: 0.5rem 0;
    overflow: hidden;
    background-color: #e5e5e5;
  }
}

#nav-profile {
  margin: 0.5em, 0.5em, 0.5em, 1em;
  .thumbnail {
    padding: 0 0.9em;
    margin: 0;
    border: 0;
  }
  img {
    max-height: 2.5rem;
  }
  .dropdown-toggle:after {
    display: none;
  }
  .dropdown-item:active {
    background-color: $secondary;
  }
}

// LEFT SIDE MENU
#side-menu {
  padding-top: 5rem;
  height: 100%;
  background-color: #1b1e24;
  position: fixed;
  .profile-block {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  img {
    width: 70px;
  }
  .profile-title {
    color: #fff;
    margin-left: 1rem;
    font-size: 1.3em;
  }
  small {
    display: block;
    color: #777;
  }
  .nav-pills {
    margin-top: 1rem;
    span:first-of-type {
      display: inline-flex;
      justify-content: center;
      width: 18px;
      margin-right: 0.7rem;
    }
    a {
      padding: 10px 15px;
      display: flex;
      align-items: center;
      color: #8b91a0;
    }
    a:hover,
    a:focus {
      color: #fff;
      background-color: inherit;
      text-decoration: none;
      will-change: transform;
    }
    a:focus .fas.pull-right {
      transform: rotate(-90deg);
    }
    .pull-right {
      margin-left: auto;
    }
    .badge {
      background-color: #1ca095;
    }
    hr {
      background-color: #252830;
    }
    ul {
      margin-left: 10%;
      a {
        display: block;
        background-color: #2b303b;
        padding: 0.6em;
        margin-bottom: 0.2rem;
        border-radius: 0.25rem;
      }
      a:hover {
        background-color: #434857;
      }
    }
  }
}

// MAIN CONTENT
#main {
  padding-top: 5rem;
}

.card {
  position: relative;
  border-radius: 0.25rem; // возможно уже есть
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.37);
  flex-grow: 5;
  margin: 1rem;
}

.card * {
  color: #252830;
}

.card-block {
  padding: 1rem;
}

.card-block h2 {
  margin: 0;
  margin-bottom: 1.5rem;
  color: #252830;
}

.round-chart {
  position: relative;
  span {
    font-size: 2.5em;
    font-weight: 500;
    line-height: 1.25rem;
    width: 6rem;
    height: 4rem;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    text-align: center;
  }
  small {
    font-size: 50%;
    font-weight: 500;
  }
}

#quick-info {
  .card-block {
    display: flex;
    flex-direction: column;
  }
  .quick-stats {
    font-size: 1.4rem;
    line-height: 2rem;
    border-bottom: 0.05rem solid #e5e9ec;
    display: flex;
    justify-content: space-between;
    span {
      font-weight: 500;
      color: #8b91a0;
    }
  }
  strong {
    font-weight: 500;
  }
}

#ration-stock {
  .stacked-area {
    height: 200px;
  }
  .switch {
    font-weight: 300;
    color: #8b91a0;
    padding: 0.5rem 0;
    display: flex;
    justify-content: space-between;
  }
}

#real-time {
  li {
    font-size: 1.2rem;
    border-bottom: 0.05rem solid #e5e9ec;    
    padding: 0.5rem 0;
  }

  li:not(:nth-child(3)) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .progress {
    margin-top: 0.25rem;
  }
  .badge {
    color: #fff;
    span {
      color: #fff;
    }
  }
  .badge-success {
    border-radius: 50%;
    padding-left: 0.4em;
    padding-right: 0.4em;    
  }
}

#daily-usage {
  .area-chart {
    height: 200px;
  }
}

#carousel-notification {
  .carousel-item {
    img {
      width: 512px;
    }
  }
  
}

#content {
  height: 30em;
  overflow: auto;
}

@media (max-width: 767px) {
  nav form#search {
    display: none;
    padding: 0.9em 1.9rem;
  }

  #nav-menu {
    justify-content: flex-start;
    padding: 1rem 0;
    .navbar-nav {
      flex-grow: 1;
    }
  }
  
  #btn-notifications {
    display: block;
    .btn {
      padding: 0 0 0.75rem 0.75rem;
      border: none;
      width: 100%;
      text-align: left;      
    }
    &>.badge {
      top: -0.5rem;
    }
  }

  #notification-list {
    max-width: 100%;
    .notification-message {
      min-width: inherit;
    }
  }  

}

@media (max-width: 512px) {
  #carousel-notification {
    .carousel-item {
      img {
        width: 300px;
      }
    }
    
  }
}